@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
.title h1{font-family:'Pacifico', cursive}.weather-holder{padding-top:40px;display:flex;justify-content:center;align-content:center}.weather-holder__widget--title{font-size:24px;font-weight:bold}.weather-holder__widget--temp{font-size:45px;font-weight:bold;text-align:center}main{padding:6rem 1rem 4rem;min-height:calc(100vh - 80px);flex:1 1}button:focus{outline:none !important}.btn-submit{background-color:transparent;border:1px solid;border-radius:20px;padding:4px 0}.btn-submit:focus{outline:none}.error{margin:0;color:red;font-size:12px;margin-top:-12px}

